import React from 'react';
import styled from 'styled-components';
import dayjs from 'dayjs';
import config from '../../../data/SiteConfig';
import { Icon } from '../atoms';
import { colors } from '../../../data/color';
const FONT_COLOR = colors.DEPRECATED_grayDark;
// TODO: atomsに移動させたい
export const CreatedAt = ({ date, containerStyle = {} }) => {
    const formatedDate = dayjs(date).format(config.dateFormat);
    return (<Wrapper containerStyle={containerStyle}>
      <Icon icon="createdAt" style={{ color: FONT_COLOR }}/>
      <Date>{formatedDate}</Date>
    </Wrapper>);
};
const Wrapper = styled.span `
  display: flex;
  align-items: center;
  ${({ containerStyle }) => `${{ ...containerStyle }}`};
`;
const Date = styled.span `
  color: ${FONT_COLOR};
`;
